/* Components: card */

.card {
  border: 0;
  width: 100%;
}

// Services
.card-service {
  background-color: transparent;
  text-align: center;
  .card-icon {
    display: block;
    width: 6rem;
    height: 6rem;
    background-color: $primary;
    border-radius: 50%;
    font-size: 3rem;
    margin: 0 auto .5rem;
    line-height: 6rem;
  }
  .card-title {

  }
  .card-description {
    color: $gray-600;
    margin-bottom: 0;
  }
}

// Testimonial
.card-testimonial {
  box-shadow: 0 0 .75rem 0 rgba(0,0,0,0.15);
  text-align: center;
  .card-body {
    padding: 2rem 2.5rem;
  }
    .card-icon {
      color: $primary;
      font-size: 2.5rem;
    }
    .card-description {
      color: $gray-600;
      margin-bottom: 0;
    }
  .card-footer {
    background-color: transparent;
    border-top: 0;
    padding-bottom: 2rem;
  }
    .card-avatar {
      border-radius: 50%;
      overflow: hidden;
      width: 64px;
      height: 64px;
      margin-bottom: .5rem;
    }
    .card-title {
      font-weight: 700;
    }
}
